import { useCallback } from "react"
import './Slider.css'

export default function Slider({min, max, initial, label, onChange}) {

  const handleChange = useCallback(event => {
    onChange(event.target.value);
  }, [onChange]);

  return <div>
    <label>
      <input type="range" min={min} max={max} value={initial} onChange={handleChange}/>
      {label}
    </label>
  </div>
}
