import { useCallback } from "react"
import { useDispatch, useSelector } from 'react-redux';
import { scheduleScreenChange } from "../../reducers/actions/game";
import TitledLayout from "../TitledLayout";
import logoSrc from "../../assets/logo_2line.png"
import LargeButton from "../LargeButton";
import Panel from "../Panel";
import Slider from "../Slider";
import { setSetting } from "../../reducers/actions/game";
import sounds from "../../assets/sounds";
import { adjustVolume } from "../../helpers/volume";
import useSound from "use-sound";

export default function Menu() {
  const dispatch = useDispatch();
  
  const gotoMenu = useCallback(() => {
    dispatch(scheduleScreenChange('menu'));
  }, [dispatch]);

  const sfxVolume = useSelector((state) => state.game.sfxVolume);
  const uiVolume = useSelector((state) => state.game.uiVolume);
  const bgmVolume = useSelector((state) => state.game.bgmVolume);

  const changeSetting = useCallback((setting, value) => {
    dispatch(setSetting(setting, value));
  }, [dispatch]);

  const [ sampleSfx ] = useSound(sounds.countdownSound1Src, { volume: adjustVolume(sfxVolume) });
  const [ sampleUi ] = useSound(sounds.buttonBlipSrc, { volume: adjustVolume(uiVolume), interrupt: false });

  const [
    setSfxVolume, 
    setUiVolume,
    setBgmVolume,
  ] = [
    useCallback((x) => {
      changeSetting('sfxVolume', x);
      sampleSfx();
    }, [changeSetting, sampleSfx]),
    useCallback((x) => {
      changeSetting('uiVolume', x)
      sampleUi();
    }, [changeSetting, sampleUi]),
    useCallback((x) => changeSetting('bgmVolume', x), [changeSetting]),
  ];
  
  return <TitledLayout className="menu-screen" title={<img className="logo" src={logoSrc} alt="Limited Space game logo"></img>}>
    <Panel>
      <h2>Settings</h2>
      <Slider label="SFX Volume" min={0} max={15} initial={sfxVolume} onChange={setSfxVolume}></Slider><br />
      <Slider label="UI Volume" min={0} max={15} initial={uiVolume} onChange={setUiVolume}></Slider><br />
      <Slider label="BGM Volume" min={0} max={15} initial={bgmVolume} onChange={setBgmVolume}></Slider><br />
    </Panel>
    <div className="main-menu-buttons">
      <LargeButton onClick={gotoMenu}>Menu</LargeButton>
    </div>
  </TitledLayout>;
}
